<template>
	<view class="content">
    <view class="content_message_success">
      <image mode="scaleToFill" src="../../static/icon/success3x.png" />
      <text class="message_success">信息上报完成</text>
    </view>
    <view class="steps-list">
      <view class="steps-list_header">
        <view class="steps-list_name">处理进度</view>
        <view class="steps-list_detail" @click="detail()">救援详情</view>
      </view>
      <view class="content-step">
        <stepsList lineNum=0 color='#fff' backgroundColor='#218AFF' :infoList='list'></stepsList>
      </view>
    </view>
    
    <!-- <view class="content_cancel">取消申请</view> -->
    <view v-if="statusDTO == 1" class="submit_cancel" @click="goEmergencyCoures">
      <button>取消申请</button>
    </view>
    <view v-if="statusDTO !== 1" class="submit_cancel" @click="handleEnd">
      <button>处理结束</button>
    </view>
    <view class="submit_save" @click="toBack">
      <button>返回首页</button>
    </view>
  </view>
</template>
<script>
import stepsList from '../../components/steps-list/steps-list.vue'
	export default {
    components: {
      stepsList
    },
		data() {
			return {
				title: 'picker',
        statusDTO: null,
        params: {},
        list: [
          {
            title: '用户申请紧急定制路线',
            date: '2022-11-05 10:22',
          },{
            title: '客服已驳回',
            date: '2022-11-05 10:22',
            opinion: '传片我单党称再非使海话局了跨时代峰峻看了看撒都看了几分离开就好儿思类类月样列反'
             
          }
        ]
			}
		},
    onLoad(options){
      this.$http.rescueInfoDetail({'id':options.id}).then(res=>{
        console.log(res)
        this.params = res && res.data
        let rescueInfoDTO = res && res.data && res.data.rescueInfoDetailDTOS
        let array = []
        rescueInfoDTO.forEach(e => {
          console.log(e)
          if(e.status == 1){
            e.title = '用户申请道路救援'
          }else if(e.status == 2){
            e.title = '已受理'
          }else if(e.status == 3){
            e.title = '协调'
          }else if(e.status == 4){
            e.title = '处理完成'
          }else if(e.status == 5){
            e.title = '已取消'
          }else if(e.status == 6){
            e.title = '已驳回'
          }else if(e.status == 9){
            e.title = '处理结束'
          }
          let plan = {
            title: e.title,
            date: e.createTime,
            rejectedRemark: e.rejectedRemark,
            status: e.status
          }
          array.push(plan)
        })
        this.list = array
        this.statusDTO = array[array.length - 1].status
      })
    },
		methods: {
      detail(){
        console.log('this.params =>',this.params)
        let params = {
          ownerName: this.params.ownerName,
          phone: this.params.phone,
          type: this.params.type,
          helpAddress: this.params.helpAddress,
          description: this.params.description,
          fileUrls: this.params.fileUrls,
          type: this.params.type,
          licensePlate: this.params.licensePlate,
          emergencyPhone: this.params.emergencyPhone,
          carOwnerDTO: this.params.carOwnerDTO,
        }
        uni.navigateTo({
          url:'/pages/rescue/rescueDetail?params='+encodeURIComponent(JSON.stringify(params))
        })
      },
      goEmergencyCoures(){
        let data = {
          id: this.params.id,
          status: 5
        }
         this.$http.rescueInfoUpdate(data).then(res=>{
          uni.switchTab({
            url:'/pages/rescue/rescueSelect'
          })
         })
      },
      handleEnd(){
        let data = {
          id: this.params.id,
          status: 9
        }
         this.$http.rescueInfoUpdate(data).then(res=>{
          uni.switchTab({
            url:'/pages/rescue/rescueSelect'
          })
        })
      },
      toBack(){
        uni.switchTab({
          url:'/pages/index/index'
        })
      },
      previewImg(index) {
        let _this = this; 
        let imgsArray = [];
        for (let i = 0; i < this.imgUrlList.length; i++) {
        if (this.imgUrlList[i].videoUrl == "") {
          imgsArray.push(this.imgUrlList[i].imgUrl);
        }
      }
      uni.previewImage({
        current: index - 1,
        urls: imgsArray,
        indicator: 'number',
        loop: true
      })
      uni.previewImage({
        current: index - 1,
        urls: imgsArray,
        indicator: 'number',
        loop: true
      })

		}
	}
}
</script>

